/* ===================== HEADER ======================= */
.header {
  padding-top: 50px;
}

.rotate {
  -webkit-transform: rotate(180deg) translateY(-3px);
  transform: rotate(180deg) translateY(-3px);
  display: inline-block;
}

.wrapper-menu {
  width: 22px;
  height: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  -webkit-transition: -webkit-transform 330ms ease-out;
  transition: -webkit-transform 330ms ease-out;
  transition: transform 330ms ease-out;
  transition: transform 330ms ease-out, -webkit-transform 330ms ease-out;
  position: relative;
  z-index: 9;
}

.open .wrapper-menu {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.line-menu {
  background-color: #111111;
  border-radius: 5px;
  width: 100%;
  height: 2px;
}

.line-menu.half {
  width: 50%;
}

.line-menu.start {
  -webkit-transition: -webkit-transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: -webkit-transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57), -webkit-transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  -webkit-transform-origin: right;
          transform-origin: right;
}

.open .line-menu.start {
  -webkit-transform: rotate(-90deg) translateX(3px);
          transform: rotate(-90deg) translateX(3px);
}

.line-menu.end {
  align-self: flex-end;
  -webkit-transition: -webkit-transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: -webkit-transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57), -webkit-transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  -webkit-transform-origin: left;
          transform-origin: left;
}

.open .line-menu.end {
  -webkit-transform: rotate(-90deg) translateX(-3px);
          transform: rotate(-90deg) translateX(-3px);
}

/* ===================== MENU + OVERLAY ================== */

.menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  opacity: 0;
  z-index: 2;
  overflow: hidden;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transition: all 330ms cubic-bezier(.17,.67,.83,.67);
  transition: all 330ms cubic-bezier(.17,.67,.83,.67);
}
.open .menu-overlay {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}

.menu-overlay nav a {
  font-family: PlayfairDisplay;
  font-size: 32px;
  padding: 0 30px;
  position: relative;
  color: #111111;
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}
.menu-overlay nav:hover a {
  color: #9c9c9c;
}
.menu-overlay nav:hover a::before {
  background: #9c9c9c;
}
.menu-overlay nav a:hover {
  text-decoration: none;
  color: #111111;
}
.menu-overlay nav a::before {
  content: '';
  width: 7px;
  height: 7px;
  display: inline-block;
  background: #111111;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: 50%;
}
.menu-overlay nav a:hover::before {
  background: #111111;
}
.menu-overlay nav a:first-child::before {
  display: none;
}
/* ===================== FOOTER ================== */
.footer {
  padding: 50px 0;
}

.social-link-group {
  position: relative;
}

.social-link-group a {
  color: #151515;
  font-size: 13px;
  text-decoration: none;
  display: inline-block;
  margin: 0 20px;
  font-weight: 500;
}

.social-link-group a:hover {
  text-decoration: none;
  color: #9c9c9c;
}

.accelator-top {
  position: absolute;
  right: 0;
  cursor: pointer;
  font-weight: 500;
}

.accelator-top::before,
.accelator-top::after {
  background: #111111;
  content: '';
  width: 20px;
  display: inline-block;
  height: 2px;
  -webkit-transition: -webkit-transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: -webkit-transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
  transition: transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57), -webkit-transform 330ms cubic-bezier(0.54, -0.81, 0.57, 0.57);
}

.accelator-top:hover::before {
  -webkit-transform: rotate(-50deg) translateY(5px);
          transform: rotate(-50deg) translateY(5px);
}

.accelator-top:hover::after {
  -webkit-transform: rotate(50deg) translateY(5px);
          transform: rotate(50deg) translateY(5px)
}
